import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import "./App.css";
import { ThemeProvider } from "styled-components";
import WebFont from "webfontloader";
import { ToastContainer } from "react-toastify";
import { GlobalStyles } from "./theme/GlobalStyles";
import { useTheme } from "./theme/useTheme";
import backgroundimage from "../src/assest/images/admin.jpg";
import Router from "./routes";
function App() {
  const location = useLocation();
  const [publiccss, setpubliccss] = useState({});
  const [image, setimage] = useState(backgroundimage);

  useEffect(() => {
    setimage(backgroundimage);
  });
  const { theme, themeLoaded, getFonts } = useTheme();
  const [selectedTheme, setSelectedTheme] = useState(theme);
  useEffect(() => {
    setSelectedTheme(theme);
  }, [themeLoaded]);

  // 4: Load all the fonts
  useEffect(() => {
    WebFont.load({
      google: {
        families: getFonts(),
      },
    });
  });
  useEffect(() => {
    if (location.pathname === "/login") {
      let updatedcss = {
        background: `url(${image})`,
        backgroundSize: "cover",
        width: "100%",
        height: "42rem",
        position: "relative",
      };
      setpubliccss((publiccss) => ({
        ...updatedcss,
      }));
    } else if (location.pathname === "/register") {
      let updatedcss = {
        background: `url(${image})`,
        backgroundSize: "cover",
        width: "100%",
        height: "50rem",
        position: "relative",
      };
      setpubliccss((publiccss) => ({
        ...updatedcss,
      }));
    } else if (location.pathname === "/Sendotp") {
      let updatedcss = {
        background: `url(${image})`,
        backgroundSize: "cover",
        width: "100%",
        height: "42rem",
        position: "relative",
      };
      setpubliccss((publiccss) => ({
        ...updatedcss,
      }));
    } else if (location.pathname === "/Forgotpassword") {
      let updatedcss = {
        background: `url(${image})`,
        backgroundSize: "cover",
        width: "100%",
        height: "42rem",
        position: "relative",
      };
      setpubliccss((publiccss) => ({
        ...updatedcss,
      }));
    } else {
      let updatedcss = {};
      setpubliccss((publiccss) => ({
        ...updatedcss,
      }));
    }
  }, [themeLoaded, location]);
  return (
    <div className="App" style={publiccss}>
      {themeLoaded && (
        <ThemeProvider theme={selectedTheme}>
          <GlobalStyles />
          <Router />
          <ToastContainer />
        </ThemeProvider>
      )}
    </div>
  );
}

export default App;
